html {
  background-color: #383e4e;
}

body {
  background-color: #383e4e;
  width: 100%;
  height: 100%;
}

header {
  background-color: #383e4e;
  color: #ffffff;
}

.App {
  font-family: sans-serif;
  text-align: center;
}

.center {
  display: table;
  justify-content: center;
}

#container2 {
  align-items: center;
  align-self: center;
  justify-content: center;
}

#cont {
  background-color: #383e4e;
  width: 100%;
  display: flex;
}

#cont2 {
  background-color: #383e4e;
  width: 100%;
  display: flex;

  justify-content: center;
}

.navbar {
  background-color: #4b4a59;
  list-style-type: none;

  color: #208f80;
}

.Card {
  position: relative;
  align-self: center;
  display: block;
  justify-content: center;
  margin-bottom: 25px;
  border-radius: 15px;
  background: #4b4a59;
  border-color: black;
  border-style: solid;
  border-width: 0px;
  padding: 20%;
  width: 30vw;
  height: 30vh;
  color: white;
  transition: transform 0.6s;
}

.cardText {
  text-align: center;
}

.Card:hover {
  background: #e6cc70;
  color: black;
  transform: scale(1.03);
  border-color: black;
}

.centralbar {
  margin: 200;
}

.navbar-title {
  font-size: 25px;
}

.clock {
  color: #e6cc70;

  transition: 0.2s;
}

.clock:hover {
  color: #dabd54;
}

.Button {
  width: 150px;
  height: 50px;
  border-radius: 10px;
  background: #4b4a59;
  border-width: 0px;
  align-self: center;
  color: white;
  font-size: 20px;
  text-align: center;
}

.Button:hover {
  background: #e6cc70;
  transform: scale(1.03);
  border-color: black;
  transition: transform 0.6s;
  color: black;
}

.YellowButton {
  width: 100%;
  height: 100px;
  font-size: 25px;
  background: #e6cc70;
  border-color: black;
  border-width: 0px;
  align-self: center;
}
.YellowButton:hover {
  background: #dabd54;
  border-color: black;
  text-decoration: underline;
  font-size: 28px;
}

.bar {
  list-style-type: none;
  background-color: #383e4e;
  color: #208f80;
  margin: 0;
  padding: 0;
}

.li {
  display: inline;
}

.backdrop {
  position: fixed;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.75);
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
}

#container-table {
  background-color: #383e4e;
  display: block;
  justify-content: center;
  align-items: center;
  align-self: center;
}

#button-table {
  margin: 0 auto;
}

.table {
  width: 80vw;
  background-color: #4b4a59;
  border-collapse: separate;
  margin: 0 auto;
  align-self: center;
  border-radius: 10px;
}

.tr {
  background-color: #4b4a59;
  padding: 8px;
}

.th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  padding: 8px;
}

.td {
  color: white;
  text-align: center;
}

tr:hover {
  background-color: #1f1e1c;
}

.notFound {
  font-size: 400%;
  color: white;
  text-align: center;
}

.pack-list {
  color: white;
  text-align: center;
}
